<script>
  export default {
    data() {
      return {
        tableData3: [{
          date: '2016-05-03',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333,
          topStatus:1,
          publishStatus:1
        }, {
          date: '2016-05-02',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333,
          topStatus:0,
          publishStatus:0
        }],
        currentPage4: 4,
        searchForm:{
          title:'',
          tag:'',
          createDate:'',
          publishDate:'',

        }
      }
    },
    methods:{
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      },
      resetForm:function(formName) {
          this.$refs[formName].resetFields();
      },
      refresh(){
        window.location.reload();
      },
      filterTop(value, row) {
        return row.topStatus === value;
      },
      filterPublish(value, row) {
        return row.publishStatus === value;
      },
    }
  }
</script>



