<template>
  <div :class="className" :id="id" :style="{height:height,width:width}"></div>
</template>

<script>
import echarts from 'echarts'
require('echarts/theme/dark') // echarts 

export default {
  name:'demo1',
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    id: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '200px'
    },
    height: {
      type: String,
      default: '200px'
    }
  },
  data() {
    return {
      chart: null
    }
  },
  mounted() {
    this.initChart()
  },
  beforeDestroy() {
    if (!this.chart) {
      return
    }
    this.chart.dispose()
    this.chart = null
  },
  methods: {
    initChart() {
      this.chart = echarts.init(document.getElementById(this.id),'dark')
      this.chart.setOption(
        {
          title: {
        text: '',
        subtext: '',
        left: 'left',
        top: 'bottom'
    },
    tooltip: {
        trigger: 'item',
        formatter: "{a} <br/>{b} : {c}%"
    },
    toolbox: {
        orient: 'vertical',
        top: 'center',
        feature: {
            dataView: {readOnly: false},
            restore: {},
            saveAsImage: {}
        }
    },
    legend: {
        orient: 'vertical',
        left: 'left',
        data: ['','','','','']
    },
    calculable: true,
    series: [
        {
            name: '',
            type: 'funnel',
            width: '40%',
            height: '45%',
            left: '5%',
            top: '50%',
            data:[
                {value: 60, name:''},
                {value: 30, name:''},
                {value: 10, name:''},
                {value: 80, name:''},
                {value: 100, name:''}
            ]
        },
        {
            name: '',
            type: 'funnel',
            width: '40%',
            height: '45%',
            left: '5%',
            top: '5%',
            sort: 'ascending',
            data:[
                {value: 60, name:''},
                {value: 30, name:''},
                {value: 10, name:''},
                {value: 80, name:''},
                {value: 100, name:''}
            ]
        },
        {
            name: '',
            type:'funnel',
            width: '40%',
            height: '45%',
            left: '55%',
            top: '5%',
            label: {
                normal: {
                    position: 'left'
                }
            },
            data:[
                {value: 60, name: ''},
                {value: 30, name: ''},
                {value: 10, name: ''},
                {value: 80, name: ''},
                {value: 100, name: ''}
            ]
        },
        {
            name: '',
            type:'funnel',
            width: '40%',
            height: '45%',
            left: '55%',
            top: '50%',
            sort: 'ascending',
            label: {
                normal: {
                    position: 'left'
                }
            },
            data:[
                {value: 60, name: ''},
                {value: 30, name: ''},
                {value: 10, name: ''},
                {value: 80, name: ''},
                {value: 100, name: ''}
            ]
        }
    ]
        })
    }
  }
}
</script>




